@font-face {
  font-family: "OpenSans-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/OpenSans-Bold.ttf") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.ttf") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.ttf") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  height: 100%;
  background-color: #c7c7c7c7;
}
body {
  min-height: 100%;
  font-family: "OpenSans-Regular", sans-serif;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  background-color: #ffffff;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  text-align: center;
}
.main-header__li {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  list-style: none;
}
.main-header {
  padding: 20px 0 20px;
}
.main-header__svg {
  position: absolute;
  right: 40%;
}
.welcome-section-header {
  background-image: url(../img/webp/hero-mobile.webp);
  background-repeat: no-repeat;
  box-sizing: border-box;
  background-size: 100%, cover;
  padding: 75px 20px 120px;
}
.welcome-section-header__h1 {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  margin: 0;
}
.main-container__li {
  background-color: #63d1bb;
  text-align: left;
  padding: 25px 30px 30px;
}
.main-container__span {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  width: 130px;
  margin: 0;
}
.main-container__span-price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
}
.main-container__link-product {
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  text-decoration: none;
  text-transform: uppercase;
}
.main-container__list {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  list-style-type: none;
}
.main-container__section-span {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #000000;
}
.main-container__ul {
  margin: 0;
  padding: 0;
}
.main-container__li--color {
  background-color: #56c3b2;
  background-image: url(../img/svg/toy-svg.svg);
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 90%;
}
.main-container__li--img {
  background-image: url(../img/svg/interior.svg);
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 90%;
}
.main-container__section-description {
  padding: 30px;
}
.main-container__table {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
.main-container__table-line {
  text-align: left;
}
.main-container__table-column {
  width: 130px;
}
.main-container__section-img {
  padding: 0 30px 30px 30px;
}
.main-container__img {
  margin-bottom: 30px;
}
.main-container__link-product {
  padding: 20px 90px 20px;
  border: 2px solid #444444;
}
.main-container__span-price {
  margin: 0;
  margin-bottom: 40px;
}
.blanks-secondary {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}
.blanks-secondary__span {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  margin: 0;
}
.blanks-secondary__paragraph {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0;
}
.blanks-secondary__h2--pozition {
  text-align: left;
  text-transform: uppercase;
}
.blanks-secondary__span--pozition {
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.blanks-secondary__paragraph--pozition {
  text-align: left;
  margin-bottom: 30px;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    width: 100%;
    justify-content: center;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    width: 100%;
    padding-left: 91px;
    padding-right: 71px;
    justify-content: center;
  }
}
.main-nav {
  display: flex;
  flex-direction: column;
  min-height: 56px;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    display: flex;
  }
}
.main-nav__wrapper {
  flex-direction: column;
  z-index: 2;
  background-color: #ffffff;
  position: absolute;
  width: 100%;
}
@media (min-width: 767px) {
  .main-nav__wrapper {
    position: absolute;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    background-color: #ffffff;
    z-index: inherit;
    width: 89%;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    display: flex;
    max-width: 1200px;
  }
}
.main-nav__nav {
  margin-left: 277px;
  margin-right: 17px;
}
@media (min-width: 768px) {
  .main-nav__nav {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__nav {
    display: none;
  }
}
.main-nav__toggle {
  width: 22px;
  height: 22px;
  position: absolute;
  z-index: 3;
  display: block;
  border: none;
  cursor: pointer;
  background-color: #ffffff;
  top: 25px;
  right: 22px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  height: 4px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__toggle {
  top: 17;
  right: 17;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  right: 1px;
  width: 22px;
  height: 4px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav--nojs .site-list__item--active a {
  padding-right: 21px;
  padding-left: 21px;
  text-align: left;
}
.main-nav--nojs .user-list__item {
  display: block;
  background-color: #000000;
}
.main-nav__section {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.site-list__link {
  text-decoration: none;
  color: #000000;
}
